/* GlitchBranding.css - Glitching effects and branding styles */

/* Glitch effect for "the glitch" branding */
.glitch-brand {
    position: relative;
    color: #1E90FF; /* Exact same blue as the description text */
    text-shadow: 
        0 0 0.5px rgba(30, 144, 255, 0.3),
        0 0 1px rgba(30, 144, 255, 0.2),
        0 0 2px rgba(30, 144, 255, 0.1),
        0 0 3px rgba(30, 144, 255, 0.05),
        0 0 4px rgba(30, 144, 255, 0.03),
        0 0 5px rgba(30, 144, 255, 0.02),
        0 0 6px rgba(30, 144, 255, 0.01);
    animation: glitchy-effect 12s infinite;
    font-weight: 300; /* Slender font weight */
    letter-spacing: 2px;
    /* Removed metallic gold background and effects */
}

.glitch-brand::before,
.glitch-brand::after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.glitch-brand::before {
    animation: glitch-effect 3s infinite linear alternate-reverse;
    color: #ffffff;
    z-index: -1;
}

.glitch-brand::after {
    animation: glitch-effect 2s infinite linear alternate-reverse;
    color: #1E90FF;
    z-index: -2;
}

/* Real glitchy effect - digital corruption and scan lines - SLOWER */
@keyframes glitchy-effect {
    0%, 100% {
        transform: translate(0);
        filter: brightness(1) contrast(1);
        background-position: 0% 50%;
    }
    5% {
        transform: translate(-2px, 2px);
        filter: brightness(1.3) contrast(1.5);
        background-position: 25% 50%;
    }
    10% {
        transform: translate(2px, -2px);
        filter: brightness(0.7) contrast(0.8);
        background-position: 50% 50%;
    }
    15% {
        transform: translate(-1px, 1px);
        filter: brightness(1.2) contrast(1.3);
        background-position: 75% 50%;
    }
    20% {
        transform: translate(1px, -1px);
        filter: brightness(0.9) contrast(0.9);
        background-position: 100% 50%;
    }
    25% {
        transform: translate(0);
        filter: brightness(1) contrast(1);
        background-position: 0% 50%;
    }
    30% {
        transform: translate(-3px, 0px);
        filter: brightness(1.4) contrast(1.6);
        background-position: 25% 50%;
    }
    35% {
        transform: translate(3px, 0px);
        filter: brightness(0.6) contrast(0.7);
        background-position: 50% 50%;
    }
    40% {
        transform: translate(0);
        filter: brightness(1) contrast(1);
        background-position: 75% 50%;
    }
    45% {
        transform: translate(0px, -3px);
        filter: brightness(1.2) contrast(1.4);
        background-position: 100% 50%;
    }
    50% {
        transform: translate(0px, 3px);
        filter: brightness(0.8) contrast(0.9);
        background-position: 0% 50%;
    }
    55% {
        transform: translate(0);
        filter: brightness(1) contrast(1);
        background-position: 25% 50%;
    }
    60% {
        transform: translate(-2px, -2px);
        filter: brightness(1.1) contrast(1.2);
        background-position: 50% 50%;
    }
    65% {
        transform: translate(2px, 2px);
        filter: brightness(0.9) contrast(0.8);
        background-position: 75% 50%;
    }
    70% {
        transform: translate(0);
        filter: brightness(1) contrast(1);
        background-position: 100% 50%;
    }
    75% {
        transform: translate(-1px, -1px);
        filter: brightness(1.3) contrast(1.4);
        background-position: 0% 50%;
    }
    80% {
        transform: translate(1px, 1px);
        filter: brightness(0.7) contrast(0.6);
        background-position: 25% 50%;
    }
    85% {
        transform: translate(0);
        filter: brightness(1) contrast(1);
        background-position: 50% 50%;
    }
    90% {
        transform: translate(-4px, 1px);
        filter: brightness(1.5) contrast(1.7);
        background-position: 75% 50%;
    }
    95% {
        transform: translate(4px, -1px);
        filter: brightness(0.5) contrast(0.6);
        background-position: 100% 50%;
    }
}

/* Glitchy red layer - digital corruption - SLOWER */
@keyframes glitchy-red {
    0%, 100% {
        opacity: 0;
        transform: translate(0);
        clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0 100%);
    }
    10% {
        opacity: 0.9;
        transform: translate(-4px, 3px);
        clip-path: polygon(0 5%, 100% 5%, 100% 95%, 0 95%);
    }
    20% {
        opacity: 0.3;
        transform: translate(4px, -3px);
        clip-path: polygon(0 15%, 100% 15%, 100% 85%, 0 85%);
    }
    30% {
        opacity: 0.8;
        transform: translate(-3px, 4px);
        clip-path: polygon(0 25%, 100% 25%, 100% 75%, 0 75%);
    }
    40% {
        opacity: 0.2;
        transform: translate(3px, -4px);
        clip-path: polygon(0 35%, 100% 35%, 100% 65%, 0 65%);
    }
    50% {
        opacity: 0;
        transform: translate(0);
        clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0 100%);
    }
    60% {
        opacity: 0.7;
        transform: translate(-5px, 2px);
        clip-path: polygon(0 10%, 100% 10%, 100% 90%, 0 90%);
    }
    70% {
        opacity: 0.4;
        transform: translate(5px, -2px);
        clip-path: polygon(0 20%, 100% 20%, 100% 80%, 0 80%);
    }
    80% {
        opacity: 0.9;
        transform: translate(-2px, 5px);
        clip-path: polygon(0 30%, 100% 30%, 100% 70%, 0 70%);
    }
    90% {
        opacity: 0.1;
        transform: translate(2px, -5px);
        clip-path: polygon(0 40%, 100% 40%, 100% 60%, 0 60%);
    }
}

/* Glitchy blue layer - scan lines and corruption - SLOWER */
@keyframes glitchy-blue {
    0%, 100% {
        opacity: 0;
        transform: translate(0);
        clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0 100%);
    }
    15% {
        opacity: 0.8;
        transform: translate(4px, -3px);
        clip-path: polygon(0 8%, 100% 8%, 100% 92%, 0 92%);
    }
    30% {
        opacity: 0.4;
        transform: translate(-4px, 3px);
        clip-path: polygon(0 16%, 100% 16%, 100% 84%, 0 84%);
    }
    45% {
        opacity: 0.9;
        transform: translate(3px, 4px);
        clip-path: polygon(0 24%, 100% 24%, 100% 76%, 0 76%);
    }
    60% {
        opacity: 0.2;
        transform: translate(-3px, -4px);
        clip-path: polygon(0 32%, 100% 32%, 100% 68%, 0 68%);
    }
    75% {
        opacity: 0;
        transform: translate(0);
        clip-path: polygon(0 0%, 100% 0%, 100% 100%, 0 100%);
    }
    90% {
        opacity: 0.6;
        transform: translate(-3px, 3px);
        clip-path: polygon(0 18%, 100% 18%, 100% 82%, 0 82%);
    }
}

/* Subtle glitch effect for smaller text */
.glitch-brand-subtle {
    color: var(--gold-dark);
    text-shadow: 
        0 0 0.5px var(--gold-dark),
        0 0 1px var(--gold-dark);
    animation: glitch-subtle 4s infinite;
}

@keyframes glitch-subtle {
    0%, 100% {
        transform: translate(0);
    }
    10% {
        transform: translate(-1px, 1px);
    }
    20% {
        transform: translate(1px, -1px);
    }
    30% {
        transform: translate(-1px, -1px);
    }
    40% {
        transform: translate(1px, 1px);
    }
}

/* Logo container */
.logo-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
}

/* Tech Glitch Logo */
.tech-logo {
    width: 60px;
    height: 60px;
    position: relative;
    border-radius: 50%;
    background: linear-gradient(45deg, #1E90FF, #1E90FF);
    box-shadow: 
        0 0 2px #1E90FF,
        0 0 4px #1E90FF;
    animation: logo-glow 3s ease-in-out infinite alternate;
    overflow: visible;
}

.tech-logo::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    height: 30px;
    background: #2A2A2A;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 
        inset 0 0 1px #1E90FF,
        0 0 1.5px #1E90FF;
}

.tech-logo::after {
    content: '';
    position: absolute;
    top: 25%;
    left: 25%;
    width: 15px;
    height: 15px;
    background: #1E90FF;
    border-radius: 50%;
    box-shadow: 0 0 1px #1E90FF;
}

/* Logo glow animation */
@keyframes logo-glow {
    0% {
        box-shadow: 
            0 0 2px #1E90FF,
            0 0 4px #1E90FF;
    }
    100% {
        box-shadow: 
            0 0 3px #1E90FF,
            0 0 6px #1E90FF,
            0 0 8px #1E90FF;
    }
}

/* Glitch effect for logo */
.tech-logo.glitch {
    animation: logo-glow 3s ease-in-out infinite alternate, logo-glitch 5s infinite;
}

@keyframes logo-glitch {
    0%, 100% {
        transform: scale(1) rotate(0deg);
    }
    10% {
        transform: scale(1.05) rotate(2deg);
    }
    20% {
        transform: scale(0.95) rotate(-1deg);
    }
    30% {
        transform: scale(1.02) rotate(1deg);
    }
    40% {
        transform: scale(0.98) rotate(-0.5deg);
    }
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .tech-logo {
        width: 50px;
        height: 50px;
    }
    
    .tech-logo::before {
        width: 25px;
        height: 25px;
    }
    
    .tech-logo::after {
        width: 12px;
        height: 12px;
    }
}

/* Glitch effect animation from Contact Us */
@keyframes glitch-effect {
    0% {
        clip-path: polygon(0 2%, 100% 2%, 100% 5%, 0 5%);
        transform: translate(-5px, -3px);
    }
    10% {
        clip-path: polygon(0 15%, 100% 15%, 100% 15%, 0 15%);
        transform: translate(5px, 3px);
    }
    20% {
        clip-path: polygon(0 10%, 100% 10%, 100% 20%, 0 20%);
        transform: translate(-3px, 1px);
    }
    30% {
        clip-path: polygon(0 1%, 100% 1%, 100% 2%, 0 2%);
        transform: translate(3px, 2px);
    }
    40% {
        clip-path: polygon(0 33%, 100% 33%, 100% 33%, 0 33%);
        transform: translate(-1px, -1px);
    }
    50% {
        clip-path: polygon(0 44%, 100% 44%, 100% 44%, 0 44%);
        transform: translate(1px, 2px);
    }
    60% {
        clip-path: polygon(0 50%, 100% 50%, 100% 20%, 0 20%);
        transform: translate(-3px, -3px);
    }
    70% {
        clip-path: polygon(0 70%, 100% 70%, 100% 70%, 0 70%);
        transform: translate(3px, 1px);
    }
    80% {
        clip-path: polygon(0 80%, 100% 80%, 100% 80%, 0 80%);
        transform: translate(-1px, -1px);
    }
    90% {
        clip-path: polygon(0 50%, 100% 50%, 100% 55%, 0 55%);
        transform: translate(3px, 3px);
    }
    100% {
        clip-path: polygon(0 90%, 100% 90%, 100% 90%, 0 90%);
        transform: translate(-3px, 0);
    }
}
